<template>
	<view class="root">
		<!-- 表单分析界面 -->
		<view class="anaylise">
			<view v-for="(item,index) in item_tables" wx-key='index' class="docker" @click="clickItem(item)">
				<view class="item">
					<image :src="'../../static/analyse/{{index}}.png'" class="imgs"></image>
					{{item.name}}
				</view>
			</view>
		</view>
		<!-- 管理人员 -->
		<view class="page__title" style="margin-top:60rpx; height: 100rpx; text-align: center; line-height: 100rpx;font-weight: bold;">管理人员名单</view>
		<view class="user_table">
		<scroll-view style="margin: 0 auto; height: 70%;"  scroll-y="true" class="scroll-Y" >
			<view class="th">
				<view class="tabel_td" style="width: 20%;">姓名</view>
				<view class="tabel_td" style="width: 30%;">联系方式</view>
				<view class="tabel_td" style="width: 20%;">职能</view>
				<view class="tabel_td" style="width: 27%;">设置</view>
			</view>
			<view class="th" v-for="(item,index) in userInfo">
				<view class="tabel_td" style="width: 20%; font-weight: 500; font-family: '宋体';">{{item.adminName}}</view>
				<view class="tabel_td" style="width: 30%; font-weight: 500; font-family: '宋体';">{{item.adminPhone}}</view>
				<view class="tabel_td" style="width: 20%; font-weight: 500; font-family: '宋体';">{{item.adminWork}}</view>
				<view class="tabel_td" style="width: 27%; font-weight: 500; font-family: '宋体';" @click="delteOrchange(item)">
					删除/修改
				</view>
			</view>
		</scroll-view>
		<view style="width: 100%; height: 20%; margin: 0 auto;">
			<navigator class="button weui-btn weui-btn_mini weui-btn_primary weui-wa-hotarea" @click="addManager" >添加管理员</navigator>
		</view>
		</view>
		<!--弹窗-->
		<modal v-if="showPop" :title='title' confirm-text="确定" cancel-text="取消" @cancel="cancelPop" @confirm="confirmPop"> 
			<input type='text' v-model="shuju" :placeholder="placeholder" class="intext" />
		</modal>
		<view :class='showDialog ? "zan-dialog--show" : ""' >
		  <!-- 如果想点击弹窗外不隐藏，取消bindtap点击事件即可 -->
		  <view class="zan-dialog__mask" @click="toggleDialog" />
		  <view class="zan-dialog__container">
		    <view>
				<view class='weui-cell'>
					<view class="weui-cell__bd">
						<input placeholder="输入姓名" @blur="addName" autocomplete="off"/>
					</view>
				</view>
				<view class='weui-cell'>
					<view class="weui-cell__bd">
						<input placeholder="输入联系方式" @blur="addPhone" autocomplete="off"/>
					</view>
				</view>
				<view class='weui-cell'>
					<view class="weui-cell__bd">
						<input placeholder="输入职能" @blur="addWork" autocomplete="off"/>
					</view>
				</view>
				<view>
					<navigator style="height: 80rpx; width: 200rpx; line-height: 80rpx;" class="button weui-btn weui-btn_mini weui-btn_primary weui-wa-hotarea" @click="configManager" >提交</navigator>
				</view>
			</view>
		  </view>
		</view>
		

		
	</view>
	
	
</template>

<script>
	import helper from '@/base.js';  
	export default {
		data() {
			return {
				host:helper.host,
				name:'',
				phone:'',
				work:'',
				showDialog: false,
				showPop:false, //弹窗
				shuju:"",
				title:"",
				placeholder:'',
				index:'',
				changeType:'',
				item_tables:[{
					"index":"1",
					"name":'外出表单分析'
				},{
					"index":"2",
					"name":"健康信息表",
				},{
					"index":"3",
					"name":"物资申请表",
				},{
					"idnex":"4",
					"name":"外来报备表",
				},{
					"index":"5",
					"name":"讨论区管理"
				},{
					"index":"6",
					"name":"增加物资"
				}],
				userInfo:[],
				deleteInde:'',
			}
		},
		
		onShow() {
			
		},
		
		methods: {
			delteOrchange:function(e){
				let _this=this
				_this.adminId=e.adminId
				_this.index=e
				uni.showActionSheet({
					itemList:["删除","修改"],
					success:function(res) {
						if(res.tapIndex==0){ /* 删除操作*/
							uni.request({
								url:_this.host+'api/admin/delete',
								data:{
									id:_this.adminId
								},
								header:{
									'token':uni.getStorageSync('token')
								},
								success(res) {
									if(res.data.code==200){
										uni.showToast({
											title:'删除成功',
											duration:1000
										})
										uni.redirectTo({
											url:'../community_control/community_control'
										})
									}else{
										uni.showToast({
											title:'删除失败',
											duration:1000,
											icon:"error"
										})
									}
								},
								fail() {
									uni.showToast({
										title:'删除失败',
										duration:1000,
										icon:"error"
									})
								}
							})
						}
						else if(res.tapIndex==1){
							uni.showActionSheet({
								itemList:['姓名','联系方式','职能'],
								success:function(res){
									var changeInfo='';
									var change="";
									if(res.tapIndex==0){
										changeInfo='修改姓名';
										change="请输入新的姓名"
										_this.changeType='adminName'
									}else if(res.tapIndex==1){
										changeInfo='修改联系方式'
										change='请输入新的联系方式'
										_this.changeType='adminPhone'
									}else if(res.tapIndex==2){
										changeInfo='修改职能'
										change='请输入新的职能'
										_this.changeType='adminWork'
									}
									_this.showPop=true
									_this.title=changeInfo
									_this.placeholder=change
								}
							})
						}
					},
					fail:function(res){
						uni.showToast({
							title:"操作失败",
							duration:2000,
						})
					}
				})
			},
			
			//弹窗
			confirmPop() { //确认
				let _this=this
				var shuju=_this.shuju
				uni.request({
					url:_this.host+'api/admin/update',
					data:{
						id:_this.adminId,
						type:_this.changeType,
						str:shuju
					},
					header:{
						'token':uni.getStorageSync('token')
					},
					success(res) {
						if(res.data.code==200){
							uni.showToast({
								title:'删除成功',
								duration:1000
							})
							uni.redirectTo({
								url:'../community_control/community_control'
							})
						}else{
							uni.showToast({
								title:'删除失败',
								duration:1000,
								icon:"error"
							})
						}
					},
					fail() {
						uni.showToast({
							title:'删除失败',
							duration:1000,
							icon:"error"
						})
					}
				})
			
			
				this.showPop = false

			},
			cancelPop() {  //取消
				console.log('点击了取消')
				this.showPop = false			  
			},
			
			// 添加管理员
			addManager:function(e){
				console.log('弹出窗口')
				this.showDialog= !this.showDialog
			},
			toggleDialog:function(e){
				this.showDialog=!this.showDialog
				console.log('sssss')
			},
			
			// 点击添加管理员按钮
			configManager:function(e){
				let _this=this
				console.log('添加管理员操作')
				uni.request({
					url:_this.host+'api/admin/addAdmin',
					method:"POST",
					data:{
						adminName:_this.name,
						adminPhone:_this.phone,
						adminWork:_this.work
					},
					header:{
						'token':uni.getStorageSync('token')
					},
					success(res) {
						if(res.data.code==200){
							wx.showToast({
								title:"添加成功",
								duration:2000
							})
							_this.userInfo=res.data.data
							_this.showDialog=!_this.showDialog
						}else{
							wx.showToast({
								title:"添加失败",
								duration:2000
							})
						}
					},
					fail() {
						wx.showToast({
							title:"添加失败",
							duration:2000
						})
					}
					
				})
				
				this.name='';
				this.phone='';
				this.work='';
			},
			
			// 添加管理员
			addName:function(e){
				this.name=e.detail.value
			},
			addPhone:function(e){
				this.phone=e.detail.value
			},
			addWork:function(e){
				this.work=e.detail.value
			},
			
			// 点击上部图片事件
			clickItem:function(e){
				var name=e.name
				console.log(name)
				let Json=JSON.stringify(e)
				let data=encodeURIComponent(Json)
				if(name=='外出表单分析'){
					wx.navigateTo({
						url:`../analyzePage/analyzePage?type=${data}`
					})
				}else if(name=='健康信息表'){
					wx.navigateTo({
						url:`../analyzePage/analyzePage?type=${data}`
					})
				}else if(name=='物资申请表'){
					wx.navigateTo({
						url:`../analyzePage/analyzePage?type=${data}`
					})
				}else if(name=='外来报备表'){
					wx.navigateTo({
						url:`../analyzePage/analyzePage?type=${data}`
					})
				}else if(name=='讨论区管理'){
					wx.navigateTo({
						url:`../analyzePage/analyzePage?type=${data}`
					})
				}else if(name=='增加物资'){
					wx.navigateTo({
						url:`../addMaterial/addMaterial`
					})
				}
			}
		}
	}
</script>

<style>
page{
	background-color: #FFFFFF;
	width:100%;
	height: 90%;
}
.root{
	width: 100%;
	height: 90%;
	margin-top: 10rpx;
}
.anaylise{
	width: 90%;
	height: 40%;
	margin: 20rpx auto;
	box-shadow: 0 0 10rpx #CCCCCC;
}
.docker{
	display: inline-block;
	margin: 0 auto;
	width:33%;
	height: 50%;
	margin:auto 0;
}
.item{
	width: 100%;
	height: 100%;
	text-align: center;
	margin:25rpx 0;
}
.imgs{
	display: block;
	width: 70rpx;
	margin: 0 auto;
	height: 70rpx
}
.user_table{
	width: 100%;
	height: 60%;
	padding: 0 auto;
}
.th{
	width: 100%;
}
.tabel_td{ 
	width: 24%;
	text-align: center;
	height: 60rpx;
	line-height:60rpx ;
	font-weight: bold;
	display: inline-block;
	box-shadow: 0 0 10rpx #CCCCCC;
	border: 1rpx solid #CCCCCC;
}
.button{
	margin: 0 auto;
	height: 100rpx;
	line-height: 100rpx;
	width: 400rpx;
	margin-top: 20rpx;
	display: block;
	letter-spacing:20rpx;
}

.zan-dialog__mask {
	  position: fixed;
	  top: 0;
	  left: 0;
	  right: 0;
	  bottom: 0;
	  z-index: 10;
	  background: rgba(0, 0, 0, 0);//设置阴影半透明背景如： background: rgba(0, 0, 0, 0.4);
	  display: none;
}

.zan-dialog__container {
	  position: fixed;
	  bottom: 400rpx;
	  width: 650rpx;//弹窗布局宽
	  height: 450rpx;//弹窗布局高，与下面弹出距离transform有关
	  margin-left: 50rpx;
	  background: #f8f8f8;
	  transform: translateY(300%);//弹框弹出距离，与弹框布局高度有关，如300%表示弹起距离为3倍弹窗高度
	  transition: all 0.4s ease;
	  z-index: 12;
	  border-radius: 20rpx;
	  box-shadow: 0px 3px 3px 2px gainsboro;//弹框的悬浮阴影效果，如不需要可注释该行
}

.zan-dialog--show .zan-dialog__container {
	  transform: translateY(0);
}

.zan-dialog--show .zan-dialog__mask {
	  display: block;
}


</style>
